.outerDiv {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1em;
}

.outerDiv > * {
    padding-bottom: 1em;
}

.playerName {
    padding-top: 0.5em;
    font-weight: bold;
}

.lvl {
    font-weight: bold
}

.prof {
    float: right;
}

.baseStats {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
    row-gap: 1em;
    column-gap: 1em;
    max-height: 55em;
}

.textEntry {
    width: 0;
    min-width: 100%;
}

@media only screen and (max-width: 600px) {
    .baseStats {
        max-height: none;
    }

    .baseStats > * {
        width: 80%;
    }

    .baseStats .proficencyScoreText {
        text-align: center;
    }
}